import { Accordion, Alert, Button } from 'react-bootstrap';
import './Bank.css';

export function Bank() {
    const list1 = [
        {
            name: '',
            bank: '',
            account: '717074-52-242733'
        },
        {
            name: '',
            bank: '',
            account: '356-0013-1202-23'
        }, {
            name: '',
            bank: '',
            account: '351-0847-6938-13'
        }
    ]
    const list2 = [
        {
            name: '',
            bank: '',
            account: '280-069176-01-011'
        },
        {
            name: '',
            bank: '',
            account: '019-13-014005'
        }, {
            name: '',
            bank: '',
            account: '01-010-3377-2176'
        }
    ]
    const listArr = (arr: any[]) => {
        let result: any[] = [];
        for (const element of arr) {
            result.push(
                <Alert className='bank-lists'>
                    <div>
                        <div>{element.name}({element.bank})</div>
                        <div>{element.account} </div>
                    </div>
                    <Button className='bank-button' onClick={() => copyAccount(element.account)} >
                        
                    </Button>
                </Alert>
            )
        }
        return result;
    }
    const copyAccount = (account: string) => {
        let result: any[] = [];
        if (!document.queryCommandSupported("copy")) {
            return alert("   .");
        }
        const textarea = document.createElement("textarea");
        textarea.value = account;
        textarea.style.top = '0';
        textarea.style.left = '0';
        textarea.style.position = "fixed";

        document.body.appendChild(textarea);
        textarea.focus();
        textarea.select();
        document.execCommand("copy");
        document.body.removeChild(textarea);
    }
    return (
        <div className='container'>
            <h1 className='title'>  </h1>
            <Accordion flush>
                <Accordion.Item eventKey="0">
                    <Accordion.Header> </Accordion.Header>
                    <Accordion.Body>
                        {
                            listArr(list1)
                        }
                    </Accordion.Body>
                </Accordion.Item>
                <Accordion.Item eventKey="1">
                    <Accordion.Header> </Accordion.Header>
                    <Accordion.Body>
                        {
                            listArr(list2)
                        }
                    </Accordion.Body>
                </Accordion.Item>
            </Accordion>
        </div>
    )
}